'use client';
import React, { useState } from 'react';
import { Cliente, DetalleServicio, VentaServicio } from '../../../entities/venta';
import { useVentas } from '../../../hooks/useVentas';
import { formatCurrency } from '../../../lib/logistica/formatters';

const clientes: Cliente[] = [
  { idCliente: 'CL-001', nombre: 'ABC Soluciones', telefono: '987654321', correo: 'abc@empresa.com' },
  { idCliente: 'CL-002', nombre: 'XYZ Market', telefono: '999888777', correo: 'xyz@empresa.com' },
];

const servicios = [
  { idServicio: 'SVC-001', nombre: 'Instalación de red', costo: 200 },
  { idServicio: 'SVC-002', nombre: 'Soporte técnico', costo: 120 },
  { idServicio: 'SVC-003', nombre: 'Cableado estructurado', costo: 300 },
];

const VentaRegistroModal = () => {
  const { registrarVenta } = useVentas();
  const [open, setOpen] = useState(false);
  const [cliente, setCliente] = useState<Cliente | null>(null);
  const [detalles, setDetalles] = useState<DetalleServicio[]>([]);
  const [descuento, setDescuento] = useState(0);

  const agregarServicio = (id: string) => {
    const servicio = servicios.find(s => s.idServicio === id);
    if (!servicio) return;
    if (detalles.some(d => d.idServicio === id)) return;
    const detalle: DetalleServicio = {
      idDetalleServicio: `DET-${Date.now()}`,
      idServicio: servicio.idServicio,
      nombreServicio: servicio.nombre,
      costoTotal: servicio.costo,
    };
    setDetalles(prev => [...prev, detalle]);
  };

  const eliminarDetalle = (id: string) => {
    setDetalles(prev => prev.filter(d => d.idServicio !== id));
  };

  const limpiar = () => {
    setCliente(null);
    setDetalles([]);
    setDescuento(0);
    setOpen(false);
  };

  const total = detalles.reduce((acc, d) => acc + d.costoTotal, 0) - descuento;

  const handleSubmit = () => {
    if (!cliente || detalles.length === 0) return;
    const venta: VentaServicio = {
      idVentaServicio: `VENTA-${Date.now()}`,
      cliente,
      detalles,
      descuento,
      costoTotal: total,
      fechaRegistro: new Date().toISOString(),
    };
    registrarVenta(venta);
    limpiar();
  };

  return (
    <>
      <button onClick={() => setOpen(true)} className="bg-indigo-600 text-white px-4 py-2 rounded">
        ➕ Registrar Venta
      </button>

      {open && (
        <div className="modal-overlay">
          <div className="modal-content">
            <button className="modal-close-btn" onClick={() => setOpen(false)}>✖</button>
            <h2 className="text-lg font-semibold mb-4">Registrar nueva venta</h2>

            {/* Cliente */}
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700">Cliente</label>
              <select
                className="border rounded w-full p-2"
                value={cliente?.idCliente || ''}
                onChange={(e) => {
                  const selected = clientes.find(c => c.idCliente === e.target.value);
                  if (selected) setCliente(selected);
                }}
              >
                <option value="">Seleccionar cliente</option>
                {clientes.map(c => (
                  <option key={c.idCliente} value={c.idCliente}>
                    {c.nombre} - {c.correo}
                  </option>
                ))}
              </select>
            </div>

            {/* Servicios */}
            <div className="mb-4">
              <p className="text-sm font-semibold mb-1">Servicios disponibles:</p>
              <div className="grid grid-cols-2 gap-2">
                {servicios.map(s => (
                  <button
                    key={s.idServicio}
                    onClick={() => agregarServicio(s.idServicio)}
                    className="border rounded p-2 text-left hover:bg-gray-100"
                  >
                    <p className="font-medium">{s.nombre}</p>
                    <p className="text-sm text-gray-500">{formatCurrency(s.costo)}</p>
                  </button>
                ))}
              </div>
            </div>

            {/* Detalles */}
            <ul className="mb-4 space-y-2">
              {detalles.map((d) => (
                <li key={d.idDetalleServicio} className="flex justify-between items-center border p-2 rounded">
                  <span>{d.nombreServicio}</span>
                  <button onClick={() => eliminarDetalle(d.idServicio)} className="text-red-500 hover:underline">Quitar</button>
                </li>
              ))}
            </ul>

            {/* Descuento */}
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700">Descuento (S/.)</label>
              <input
                type="number"
                className="border p-2 rounded w-full"
                value={descuento}
                min={0}
                onChange={(e) => setDescuento(Number(e.target.value))}
              />
            </div>

            {/* Total */}
            <div className="mb-4 text-right font-bold text-green-700">
              Total: {formatCurrency(total)}
            </div>

            {/* Acciones */}
            <div className="flex justify-end gap-2">
              <button onClick={limpiar} className="bg-gray-200 text-gray-800 px-4 py-2 rounded">Cancelar</button>
              <button onClick={handleSubmit} className="bg-blue-600 text-white px-4 py-2 rounded" disabled={!cliente || detalles.length === 0}>
                Registrar
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default VentaRegistroModal;
