// src/hooks/useVentas.ts
'use client';

import { useState, useEffect } from 'react';
import { VentaServicio } from '../entities/venta';
import { ventasMock } from '../mocks/ventas.mock';

// Hook personalizado para gestionar ventas
export const useVentas = () => {
  const [ventas, setVentas] = useState<VentaServicio[]>([]);

  // Simular carga inicial (puedes reemplazar esto con una llamada a API en el futuro)
  useEffect(() => {
    const stored = localStorage.getItem('ventas');
    if (stored) {
      setVentas(JSON.parse(stored));
    }
  }, []);

  // Guardar ventas en localStorage cuando cambien
  useEffect(() => {
    localStorage.setItem('ventas', JSON.stringify(ventas));
  }, [ventas]);

  // Registrar una nueva venta
  const registrarVenta = (venta: VentaServicio) => {
    setVentas((prev) => [...prev, venta]);
  };

  // Eliminar una venta por ID
  const eliminarVenta = (idVenta: string) => {
    setVentas((prev) => prev.filter((v) => v.idVentaServicio !== idVenta));
  };

  return {
    ventas,
    registrarVenta,
    eliminarVenta,
  };
};
