'use client';
import React from 'react';
import { useVentas } from '../../../hooks/useVentas';
import { formatCurrency } from '../../../lib/logistica/formatters';

const VentaResumen = () => {
  const { ventas } = useVentas();
  const totalVentas = ventas.length;
  const totalIngresos = ventas.reduce((acc, v) => acc + v.costoTotal, 0);
  const totalDescuentos = ventas.reduce((acc, v) => acc + v.descuento, 0);

  return (
    <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
      <ResumenCard title="🧾 Total Ventas" value={totalVentas} color="text-indigo-600" />
      <ResumenCard title="💰 Ingresos Totales" value={formatCurrency(totalIngresos)} color="text-green-600" />
      <ResumenCard title="🔻 Descuentos Aplicados" value={formatCurrency(totalDescuentos)} color="text-red-600" />
    </div>
  );
};

const ResumenCard = ({ title, value, color }: { title: string; value: string | number; color: string }) => (
  <div className="bg-white p-4 rounded-lg shadow border">
    <h4 className="text-sm text-gray-500">{title}</h4>
    <p className={`text-2xl font-bold ${color}`}>{value}</p>
  </div>
);

export default VentaResumen;
